    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -webkit-flex-direction: column;
    -ms-flex-direction: column;
    flex-direction: column
}

.work .loginBox .image {
    width: 166px
}

.work .loginBox .loginTips {
    margin-top: 14px;
    font-size: 14px;
    color: #847da4
}

.work .loginBox .loginBtn {
    margin-top: 28px;
    width: 120px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    font-size: 16px;
    color: #fff;
    background: #6e4ff4;
    border-radius: 8px;
    cursor: pointer
}

.work .loginBox .loginBtn:hover {
    background: #5439ce
}

.work .loginBox .loginBtn:active {
    background: #5118ce
}

